"use client";

import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

const colors = [
    { name: "Giallo Orion", hex: "#F5C211", accent: "#FFD700" },
    { name: "Arancio Borealis", hex: "#FF5F00", accent: "#FF8C00" },
    { name: "Verde Mantis", hex: "#48DF0C", accent: "#7CFC00" },
];

export default function Configurator() {
    const [isOpen, setIsOpen] = useState(false);
    const [activeColor, setActiveColor] = useState(colors[0]);

    const handleColorChange = (color: typeof colors[0]) => {
        setActiveColor(color);
        document.documentElement.style.setProperty("--color-lambo-yellow", color.hex);
        document.documentElement.style.setProperty("--color-lambo-accent", color.accent);
    };

    return (
        <>
            {/* TRIGGER BUTTON */}
            <motion.button
                className="fixed bottom-10 right-10 z-[60] bg-lambo-yellow text-black font-heading px-6 py-2 clip-angle font-bold hover:bg-white transition-colors shadow-lg shadow-lambo-yellow/20"
                onClick={() => setIsOpen(!isOpen)}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
            >
                {isOpen ? "CLOSE" : "CONFIGURE"}
            </motion.button>

            {/* PANEL */}
            <AnimatePresence>
                {isOpen && (
                    <motion.div
                        className="fixed bottom-24 right-10 z-[60] bg-black/80 backdrop-blur-xl border border-white/10 p-6 clip-button w-80"
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: 20 }}
                    >
                        <h3 className="text-white font-heading mb-4 text-sm tracking-widest border-b border-white/10 pb-2">
                            SELECT COLOR
                        </h3>

                        <div className="flex gap-4 mb-8">
                            {colors.map((color) => (
                                <button
                                    key={color.name}
                                    className={`w-10 h-10 rounded-full border-2 transition-all duration-300 relative group`}
                                    style={{
                                        backgroundColor: color.hex,
                                        borderColor: activeColor.name === color.name ? "white" : "transparent"
                                    }}
                                    onClick={() => handleColorChange(color)}
                                    title={color.name}
                                >
                                    {activeColor.name === color.name && (
                                        <motion.div
                                            layoutId="activeRing"
                                            className="absolute -inset-2 rounded-full border border-white/50"
                                        />
                                    )}
                                </button>
                            ))}
                        </div>

                        <h3 className="text-white font-heading mb-4 text-sm tracking-widest border-b border-white/10 pb-2">
                            ENGINE
                        </h3>

                        <button className="w-full py-3 bg-red-600/20 border border-red-500 text-red-500 font-heading hover:bg-red-600 hover:text-white transition-all hover:shadow-[0_0_20px_rgba(220,38,38,0.5)]">
                            START V12
                        </button>

                    </motion.div>
                )}
            </AnimatePresence>
        </>
    );
}
