"use client";

import { motion, useTransform, MotionValue } from "framer-motion";
import { carData } from "@/data/carData";

interface Props {
    scrollYProgress: MotionValue<number>;
}

export default function ExperienceOverlay({ scrollYProgress }: Props) {
    // Hero Opacity & Scale
    const heroOpacity = useTransform(scrollYProgress, [0, 0.25, 0.3], [1, 1, 0]);
    const heroScale = useTransform(scrollYProgress, [0, 0.3], [1, 1.1]);

    // Design Opacity & Position
    const designOpacity = useTransform(scrollYProgress, [0.35, 0.45, 0.6, 0.65], [0, 1, 1, 0]);
    const designX = useTransform(scrollYProgress, [0.35, 0.45], [-100, 0]);

    // Engine Opacity & Position (Widened Range & Earlier Trigger)
    const engineOpacity = useTransform(scrollYProgress, [0.55, 0.65, 0.95], [0, 1, 1]);
    const engineY = useTransform(scrollYProgress, [0.55, 0.65], [100, 0]);

    return (
        <div className="absolute inset-0 pointer-events-none z-50 overflow-hidden">
            {/* HERO SECTION */}
            <motion.div
                className="absolute top-[20%] left-10 md:left-20 max-w-2xl"
                style={{ opacity: heroOpacity, scale: heroScale }}
            >
                <motion.h1
                    className="text-6xl md:text-9xl font-black italic text-transparent bg-clip-text bg-gradient-to-r from-white to-gray-500 mb-4 tracking-tighter"
                    initial={{ x: -100, opacity: 0 }}
                    animate={{ x: 0, opacity: 1 }}
                    transition={{ duration: 0.8, ease: "circOut" }}
                >
                    {carData.hero.title}
                </motion.h1>
                <motion.p
                    className="text-xl md:text-2xl text-lambo-yellow font-heading tracking-[0.5em] border-l-4 border-lambo-yellow pl-6"
                    initial={{ x: 100, opacity: 0 }}
                    animate={{ x: 0, opacity: 1 }}
                    transition={{ delay: 0.5, duration: 0.8 }}
                >
                    {carData.hero.subtitle}
                </motion.p>
                <p className="mt-8 text-white/60 max-w-lg leading-relaxed">{carData.hero.description}</p>
            </motion.div>

            {/* DESIGN DETAILS */}
            <motion.div
                className="absolute top-[40%] right-10 md:right-20 text-right"
                style={{ opacity: designOpacity, x: designX }}
            >
                <div className="relative inline-block">
                    <div className="absolute -inset-4 border border-lambo-yellow/30 clip-angle opacity-50" />
                    <h2 className="text-5xl md:text-7xl font-bold bg-lambo-yellow text-black px-6 py-2 transform -skew-x-12 inline-block shadow-[10px_10px_0px_rgba(255,255,255,0.1)]">
                        {carData.design.title}
                    </h2>
                </div>
                <h3 className="text-2xl mt-6 font-heading tracking-widest text-white/80">{carData.design.subtitle}</h3>
                <p className="mt-4 text-white/50 max-w-md ml-auto">{carData.design.description}</p>

                {/* Specs Grid */}
                <div className="grid grid-cols-2 lg:grid-cols-3 gap-6 mt-8 border-t border-white/10 pt-6 justify-end">
                    {carData.design.specs.map((spec, i) => (
                        <div key={i} className="text-right">
                            <p className="text-lambo-yellow text-xs tracking-widest font-heading mb-1">{spec.label}</p>
                            <p className="text-2xl font-bold font-heading italic">{spec.value}</p>
                        </div>
                    ))}
                </div>
            </motion.div>

            {/* ENGINE SPECS & FEATURES HUD */}
            <motion.div
                className="absolute inset-0 pointer-events-none"
                style={{ opacity: engineOpacity }}
            >
                {/* Main Engine Stats - Bottom Left */}
                <motion.div
                    className="absolute bottom-[15%] left-5 md:left-20"
                    style={{ y: engineY }}
                >
                    <div className="flex items-center gap-4 mb-2">
                        <div className="w-2 h-2 bg-lambo-yellow rounded-full animate-pulse" />
                        <h2 className="text-xl md:text-3xl font-bold italic text-white tracking-widest">
                            {carData.engine.title}
                        </h2>
                    </div>

                    <div className="relative pl-6 border-l-2 border-lambo-yellow/50">
                        {/* Decorative HUD Elements */}
                        <div className="absolute top-0 left-0 w-2 h-full bg-lambo-yellow/10" />
                        <div className="absolute top-0 -left-1 w-1 h-1 bg-lambo-yellow" />
                        <div className="absolute bottom-0 -left-1 w-1 h-1 bg-lambo-yellow" />

                        <h3 className="text-sm text-lambo-yellow font-mono mb-6 tracking-widest">{carData.engine.subtitle}</h3>

                        <div className="grid grid-cols-2 gap-x-12 gap-y-6">
                            {carData.engine.specs.map((spec, i) => (
                                <div key={i} className="relative group">
                                    <div className="absolute -left-3 top-1 w-1 h-1 bg-white/30" />
                                    <p className="text-[10px] text-white/50 tracking-[0.2em] mb-1 uppercase">{spec.label}</p>
                                    <p className="text-2xl md:text-4xl font-black italic text-white group-hover:text-lambo-yellow transition-colors duration-300">
                                        {spec.value}
                                    </p>
                                </div>
                            ))}
                        </div>
                    </div>
                </motion.div>

                {/* Tech Features List - Right Side Slide-in */}
                <motion.div
                    className="absolute top-[30%] right-5 md:right-20 text-right"
                    style={{ x: useTransform(scrollYProgress, [0.55, 0.65], [100, 0]) }}
                >
                    <div className="inline-block border-b-2 border-lambo-yellow/50 pb-2 mb-6">
                        <h3 className="text-lg text-white font-bold tracking-widest italic">SYSTEM DIAGNOSTICS</h3>
                    </div>

                    <div className="flex flex-col gap-4 items-end">
                        {["ACTIVE AERODYNAMICS", "TITANIUM EXHAUST", "SEQUENTIAL GEARBOX"].map((feature, i) => (
                            <div key={i} className="flex items-center gap-3">
                                <span className="text-sm text-white/70 font-mono tracking-wider">{feature}</span>
                                <div className="w-1 h-1 bg-lambo-yellow" />
                                <div className={`h-[1px] bg-white/20 transition-all duration-1000 delay-${i * 200}`} style={{ width: "30px" }} />
                            </div>
                        ))}
                    </div>
                </motion.div>
            </motion.div>
        </div>
    );
}
