"use client";

import { useMotionValueEvent, useScroll } from "framer-motion";
import { useState } from "react";
import clsx from "clsx";

export default function Navbar() {
    const { scrollY } = useScroll();
    const [scrolled, setScrolled] = useState(false);

    useMotionValueEvent(scrollY, "change", (latest) => {
        setScrolled(latest > 50);
    });

    return (
        <nav
            className={clsx(
                "fixed top-0 left-0 w-full z-50 transition-all duration-500 border-b border-transparent",
                scrolled ? "bg-lambo-black/80 backdrop-blur-md border-white/10 py-4" : "py-8 bg-transparent"
            )}
        >
            <div className="container mx-auto px-6 flex justify-between items-center">
                {/* LOGO */}
                <div className="flex items-center gap-2">
                    <span className="font-heading text-xl md:text-2xl font-bold italic tracking-wide text-white">
                        LAMBORGHINI
                    </span>
                    <span className="text-lambo-yellow text-xs tracking-widest self-end mb-1">AUTOMOBILI</span>
                </div>

                {/* CTA BUTTON */}
                <button className="bg-lambo-yellow text-black font-heading font-black tracking-wider px-8 py-3 hover:bg-white transition-colors duration-300 clip-button text-sm">
                    INQUIRE
                </button>
            </div>
        </nav>
    );
}
